import React, { useState } from 'react';
import ProjectDetailsModal, { ProjectDetailData } from './ProjectDetailsModal';
import * as models from './models';

interface State {
  deps: ProjectDetailData;
  detailsModalShow: boolean;
}

interface Props {
  resumeProjects: models.Project[];
  resumeBasicInfo: models.BasicInfo;
}

const Projects = (props: Props) => {
  const [state, setState] = useState<State>({
    deps: {
      description: '',
      images: [],
      technologies: [],
      title: '',
      url: '',
    },
    detailsModalShow: false,
  });

  const detailsModalShow = (data: any) => {
    setState({ detailsModalShow: true, deps: data });
  };

  const detailsModalClose = () =>
    setState({ ...state, detailsModalShow: false });

  const sectionName = props.resumeBasicInfo.sectionName.projects;
  const projects = props.resumeProjects.map(project => (
    <div
      className="col-sm-12 col-md-6 col-lg-4"
      key={project.title}
      style={{ cursor: 'pointer' }}
    >
      <span className="portfolio-item d-block">
        <div className="foto" onClick={() => detailsModalShow(project)}>
          <div>
            <img
              src={project.images[0]}
              alt="projectImages"
              height="230"
              style={{
                marginBottom: 0,
                paddingBottom: 0,
                position: 'relative',
              }}
            />
            <span className="project-date">{project.startDate}</span>
            <br />
            <p className="project-title-settings mt-3">{project.title}</p>
          </div>
        </div>
      </span>
    </div>
  ));

  return (
    <section id="portfolio">
      <div className="col-md-12">
        <h1 className="section-title" style={{ color: 'black' }}>
          <span>{sectionName}</span>
        </h1>
        <div className="col-md-12 mx-auto">
          <div className="row mx-auto">{projects}</div>
        </div>
        <ProjectDetailsModal
          show={state.detailsModalShow}
          onHide={detailsModalClose}
          data={state.deps}
        />
      </div>
    </section>
  );
};

export default Projects;
