import React, { useEffect, useState } from 'react';
import ReactGA from 'react-ga';
import Header from './components/Header';
import Footer from './components/Footer';
import About from './components/About';
import Experience from './components/Experience';
import Projects from './components/Projects';
import Skills from './components/Skills';
import axios from 'axios';
import './App.scss';
import * as models from './components/models';

declare global {
  interface Window {
    language: {
      primaryLanguage: string;
      secondaryLanguage: string;
      primaryLanguageIconId: string;
      secondaryLanguageIconId: string;
    };
  }
}

window.language = window.language || {};

interface ResumeData {
  basicInfo: models.BasicInfo;
  projects: models.Project[];
  experiences: models.Experience[];
}

interface SharedData {
  basicInfo: models.Info;
  skills: { icons: [] };
}

const App: React.FC = () => {
  const [resumeData, setResumeData] = useState<ResumeData>({
    basicInfo: {
      description: '',
      descriptionHeader: '',
      sectionName: {
        about: 'About me',
        projects: 'Projects',
        skills: 'Skills',
        experience: 'Experience',
      },
    },
    projects: [],
    experiences: [],
  });

  const [sharedData, setSharedData] = useState<SharedData>({
    basicInfo: {
      name: 'binh',
      titles: [],
      socialNetwork: [],
      image: '',
    },
    skills: {
      icons: [],
    },
  });

  const applyPickedLanguage = (
    pickedLanguage: string,
    oppositeLangIconId: string
  ) => {
    swapCurrentlyActiveLanguage(oppositeLangIconId);
    document.documentElement.lang = pickedLanguage;
    const resumePath =
      document.documentElement.lang === window.language.primaryLanguage
        ? `res_primary_language.json`
        : `res_secondary_language.json`;
    loadResumeFromPath(resumePath);
  };

  const swapCurrentlyActiveLanguage = (oppositeLangIconId: string) => {
    const { language } = window;
    const pickedLangIconId =
      oppositeLangIconId === language.primaryLanguageIconId
        ? language.secondaryLanguageIconId
        : language.primaryLanguageIconId;
    document.getElementById(oppositeLangIconId)!.removeAttribute('filter');
    document
      .getElementById(pickedLangIconId)!
      .setAttribute('filter', 'brightness(40%)');
  };

  const loadResumeFromPath = async (path: string) => {
    try {
      const res = await axios.get(path);
      setResumeData({ ...res.data });
    } catch (error) {
      alert(error);
    }
  };

  useEffect(() => {
    const loadSharedData = async () => {
      try {
        const res = await fetch(`portfolio_shared_data.json`);
        const json = await res.json();
        setSharedData({ ...json });
      } catch (error) {
        alert(error);
      }
    };
    loadSharedData();
    applyPickedLanguage(
      window.language.primaryLanguage,
      window.language.secondaryLanguageIconId
    );
    const TRACKING_ID = 'UA-201839371-1';
    ReactGA.initialize(TRACKING_ID);
    ReactGA.pageview(window.location.pathname);
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div>
      <Header sharedData={sharedData.basicInfo} />
      <div className="col-md-12 mx-auto text-center language">
        <div
          onClick={() =>
            applyPickedLanguage(
              window.language.primaryLanguage,
              window.language.secondaryLanguageIconId
            )
          }
          style={{ display: 'inline' }}
        >
          <span
            className="iconify language-icon mr-5"
            data-icon="twemoji-flag-for-flag-united-kingdom"
            data-inline="false"
            id={window.language.primaryLanguageIconId}
          />
        </div>
        <div
          onClick={() =>
            applyPickedLanguage(
              window.language.secondaryLanguage,
              window.language.primaryLanguageIconId
            )
          }
          style={{ display: 'inline' }}
        >
          <span
            className="iconify language-icon"
            data-icon="twemoji-flag-for-flag-vietnam"
            data-inline="false"
            id={window.language.secondaryLanguageIconId}
          />
        </div>
      </div>
      <About
        resumeBasicInfo={resumeData.basicInfo}
        sharedBasicInfo={sharedData.basicInfo}
      />
      <Projects
        resumeProjects={resumeData.projects}
        resumeBasicInfo={resumeData.basicInfo}
      />
      <Skills
        sharedSkills={sharedData.skills}
        resumeBasicInfo={resumeData.basicInfo}
      />
      <Experience
        resumeExperience={resumeData.experiences}
        resumeBasicInfo={resumeData.basicInfo}
      />
      <Footer sharedBasicInfo={sharedData.basicInfo} />
    </div>
  );
};

export default App;
