import React from 'react';
import { Modal } from 'react-bootstrap';
import * as models from './models';
import AwesomeSlider from 'react-awesome-slider';
import * as AwesomeSliderStyles from '../scss/light-slider.scss';
import * as AwesomeSliderStyles2 from '../scss/dark-slider.scss';
import 'react-awesome-slider/dist/custom-animations/scale-out-animation.css';

export interface ProjectDetailData {
  technologies: models.TechIcon[];
  images: string[];
  title: string;
  description: string;
  url: string;
}

interface Props {
  data: ProjectDetailData;
  onHide: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;
  show: boolean;
}

const ProjectDetailsModal = (props: Props) => {
  const technologies = props.data.technologies || [];
  const images = props.data.images;
  const title = props.data.title;
  const description = props.data.description;
  const url = props.data.url;

  const tech = technologies.map((icon: any, i: number) => {
    return (
      <li className="list-inline-item mx-3" key={i}>
        <span>
          <div className="text-center">
            <i className={icon.class} style={{ fontSize: '300%' }}>
              <p className="text-center" style={{ fontSize: '30%' }}>
                {icon.name}
              </p>
            </i>
          </div>
        </span>
      </li>
    );
  });

  const img = images.map((elem, i) => {
    return <div key={i} data-src={elem} />;
  });

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      className="modal-inside"
    >
      <span onClick={props.onHide} className="modal-close">
        <i className="fas fa-times fa-3x close-icon" />
      </span>
      <div className="col-md-12">
        <div className="col-md-10 mx-auto" style={{ paddingBottom: '50px' }}>
          <div className="slider-tab">
            <span
              className="iconify slider-iconfiy"
              data-icon="emojione:red-circle"
              data-inline="false"
              style={{ marginLeft: '5px' }}
            />{' '}
            &nbsp;{' '}
            <span
              className="iconify slider-iconfiy"
              data-icon="twemoji:yellow-circle"
              data-inline="false"
            />{' '}
            &nbsp;{' '}
            <span
              className="iconify slider-iconfiy"
              data-icon="twemoji:green-circle"
              data-inline="false"
            />
          </div>
          <AwesomeSlider
            cssModule={[AwesomeSliderStyles, AwesomeSliderStyles2]}
            animation="scaleOutAnimation"
            className="slider-image"
          >
            {img}
          </AwesomeSlider>
        </div>
        <div className="col-md-10 mx-auto">
          <h3 style={{ padding: '5px 5px 0 5px' }}>
            {title}
            {url ? (
              <a
                href={url}
                target="_blank"
                rel="noopener noreferrer"
                className="link-href"
              >
                <i
                  className="fas fa-external-link-alt"
                  style={{ marginLeft: '10px' }}
                />
              </a>
            ) : null}
          </h3>
          <p className="modal-description">{description}</p>
          <div className="col-md-12 text-center">
            <ul className="list-inline mx-auto">{tech}</ul>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default ProjectDetailsModal;
